<template>
  <div class="test_json">
    <v-container>
      <v-layout wrap>
        <v-flex xs12 sm12 md3>
          <v-card width="100%">
            <v-subheader>Selected city from bottm select box</v-subheader>
            <v-btn block>
              <select style="width:100%; text-align:center;" v-on:change="select_city">
                <option large v-for="input_city in input_cities" v-bind:value="input_city" :key="input_city">
                  {{ input_city }}
                </option>
              </select>
            </v-btn>
          </v-card>
          <div style="height: 350px; width: 100%; overflow: auto;">
            <l-map :zoom="zoom" :center="selected_city_geodata.slice(1.3)" :attribution="attribution"
              :options="{zoomControl: true}">
              <l-tile-layer :url="url"></l-tile-layer>
              <l-marker :lat-lng="selected_city_geodata.slice(1.3)">
                <l-tooltip>{{selected_city_geodata}}</l-tooltip>
              </l-marker>
            </l-map>
          </div>
        </v-flex>
        <v-flex xs12 sm12 md3>
          <v-subheader>Rader chart in {{selected_city_column[0]}}</v-subheader>
          <apexchart type=radar height=350px :options="radar_chartOptions" :series="radar_series" />
        </v-flex>
        <v-flex xs12 sm12 md6>
          <div style="height: 460px; width: 100%; overflow: auto;">
            <v-subheader>Selected city: {{selected_city_column[0]}}</v-subheader>
            <!--
            <p>selected_city_column:{{selected_city_column}}</p>
            <p>{{check1}}</p>
            <p>{{check2}}</p>
            <p>{{check3}}</p>
            -->
            <v-card style="padding : 5% ;">
              <v-list three-line>
                <v-list-tile v-for="(item, index) in first_column_input_data.slice( 1, 18 )" :key="item.id">
                  <div style="width: 100%">
                    <v-subheader>{{first_column_input_data[index+1]}}</v-subheader>
                  </div>
                  <div style="width: 100%">
                    <v-slider min="0" step="0.1" thumb-label="always" :thumb-size="24"
                      v-model="selected_city_column[index+1]"></v-slider>
                  </div>
                  <div style="padding : 5%; width: 30%">
                    <v-text-field v-model="selected_city_column[index+1]">
                      {{selected_city_column[index+1]}}</v-text-field>
                  </div>
                </v-list-tile>
              </v-list>
            </v-card>
          </div>
        </v-flex>
      </v-layout>
      <v-layout wrap>
        <v-flex xs12 sm12 md12>
          <div style="height: 500px; width: 100%; overflow: auto;">
            <h2>Input data</h2>
            <hot-table v-model="input_data" :data="input_data" ref="input_data_ref" :autoWrapRow="false"
              :colHeaders="true" :rowHeaders="true" :readOnly="false" style="width: auto; height: auto; overflow: auto"
              licenseKey="non-commercial-and-evaluation"></hot-table>
          </div>
          <div class="box14" style="height: 200px; width: 100%; overflow: auto;">
            <h2>Raws</h2>
            <p>selected_city: {{selected_city}}</p>
            <p>selected_city_column: {{selected_city_column}}</p>
            <p>first_column_input_data: {{first_column_input_data}}</p>
            <p>Max_column:{{Max_column}}</p>
            <p>Min_column:{{Min_column}}</p>
            <p>GHG:{{GHG}}</p>
            <p>mu:{{mu}}</p>
            <p>Social_Welfare:{{Social_Welfare}}</p>
            <p>radar_series:{{radar_series}}</p>
          </div>
          <div style="height: auto; width: 100%; overflow: auto;">
            <h2>Input geodata</h2>
            <hot-table :data="input_geodata" ref="input_geodata_ref" :colHeaders="true" :rowHeaders="true"
              :readOnly="true" style="width: auto; height: auto; overflow: auto"
              licenseKey="non-commercial-and-evaluation"></hot-table>
          </div>
          <h2>Input calculation</h2>
          <hot-table :data="input_calculation" :readOnly="true" ref="input_calculation_ref" :colHeaders="true" :rowHeaders="true"
            style="width: auto; height: auto; overflow: auto" licenseKey="non-commercial-and-evaluation"></hot-table>

          <h2>Input graph</h2>
          <hot-table :data="input_graph" :readOnly="true" ref="input_graph_ref" :colHeaders="true" :rowHeaders="true"
            style="width: auto; height: auto; overflow: auto" licenseKey="non-commercial-and-evaluation"></hot-table>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
  import input_json_data from '../assets/JSON_Data/input_data.json'
  import input_json_cities from '../assets/JSON_Data/input_cities.json'
  import input_json_calculation from '../assets/JSON_Data/input_calculation_data.json'
  import input_json_graph from '../assets/JSON_Data/input_graph_data.json'
  import input_json_geodata from '../assets/JSON_Data/input_geodata.json'

  import HotTable from 'vue-handsontable-official'
  import compute_gmean from 'compute-gmean'
  import {
    LMap,
    LTileLayer,
    LMarker,
    LTooltip,
    LIcon,
  } from 'vue2-leaflet';

  function pushTwoDimensionalArray(array1, array2, axis) {
    if (axis != 1) axis = 0;
    if (axis == 0) { //　縦方向の追加
      for (var i = 0; i < array2.length; i++) {
        array1.push(array2[i]);
      }
    } else { //　横方向の追加
      for (var i = 0; i < array1.length; i++) {
        Array.prototype.push.apply(array1[i], array2[i]);
      }
    }
    return array1;
  }

  /*大小比較 method */
  function MaxSort(Max_col, col) {
    for (var i = 0; i < Max_col.length; i++) {
      if (col[i] > Max_col[i]) {
        Max_col[i] = col[i];
      }
    }
    return Max_col;
  }

  function MinSort(Min_col, col) {
    for (var i = 0; i < Min_col.length; i++) {
      if (col[i] < Min_col[i]) {
        Min_col[i] = col[i];
      }
    }
    return Min_col;
  }


  /* Get geomean calculation from 1D array method */
  const transpose = a => a[0].map((_, c) => a.map(r => r[c]));

  /* Get geomean calculation from 1D array method */
  var gmean = require('compute-gmean')

  /* Get column from table method */
  function getCol(matrix, col) {
    var column = [];
    for (var i = 0; i < matrix.length; i++) {
      column.push(matrix[i][col]);
    }
    return column;
  }

  /* Get average from array */
  var average = function (arr) {
    if (typeof arr !== 'object' || arr.length === 0) return false;

    var key, totalNumber = 0;
    for (key in arr) totalNumber = totalNumber + Number(arr[key]);

    return totalNumber / arr.length;
  };

  /* Get (value-maxGHG)/(minGHG-maxGHG) */
  function Emission(value, maxGHG, minGHG) {
    return (value - maxGHG) / (minGHG - maxGHG);
  }
  /* Get Social Welfare method (HDI * Health Index)*0.5 */
  function Social_Welfare(HDI, Health_index) {
    return (HDI * Health_index) * 0.5;
  }

  /* Get USDI method */
  function USDI(EaC, CP, Economic, SW) {
    return (EaC + CP + Economic + SW) * 0.25;
  }

  export default {
    components: {
      HotTable,
      LMap,
      LTileLayer,
      LMarker,
      LTooltip,
      LIcon,
    },
    data: function () {
      return {
        input_data: input_json_data,
        input_cities: input_json_cities,
        input_calculation: input_json_calculation,
        input_graph: input_json_graph,
        input_geodata: input_json_geodata,
        table: 0,
        selected_city: "",
        selected_data: input_json_data,
        selected_city_geodata: ["Tokyo", "35.689487", "139.691711"],
        selected_city_column: ["Tokyo", 4.8, 40, 6, 33, 1, 0, 1, 100, 100, 11, 1, 100, 100, 100, 4, 1, 1],
        matrix: [],
        before_matrix: [],
        after_matrix: [],
        merged_matrix: [],
        check1: [],
        check2: [],
        check3: [],
        max_column: [10, 86, 57.3, 100, 14.8, 0, 1, 100, 100, 100, 1, 100, 100, 100, 14, 0.9, 1],
        min_column: [0.5, 18.5, 5.7, 27, 1.2, 0, 0, 0, 0, 0, 0, 0, 1000, 8900, 2.2, 0.3, 0.2],
        avarage_column: [4.6, 46.7, 22.5, 107.8, 6, 0, 1, 0.599, 70.1, 38.6, 0.17, 82.8, 9800, 52000, 5, 0.791,
          0.856259512
        ],

        attribution: "<a href='https://maps.gsi.go.jp/development/ichiran.html' target='_blank'>地理院タイル</a>",
        url: 'https://cyberjapandata.gsi.go.jp/xyz/pale/{z}/{x}/{y}.png',
        zoom: 3,
        icon: L.icon({
          iconUrl: 'static/images/baseball-marker.png',
          iconSize: [32, 37],
          iconAnchor: [16, 37]
        }),

        url: 'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
        radar_series: [{
          name: 'Selected City',
          data: [0.5, 0.5, 0.5, 0.5],
        }, {
          name: 'Average 22 cities',
          data: [0.65, 0.22, 0.33, 0.50],
        }],
        radar_chartOptions: {
          labels: ['Energy and Climate', 'City Plannig', 'Economic', 'Social Welfare'],
          title: {
            text: ''
          },
          dropShadow: {
            enabled: true,
            blur: 1,
            left: 1,
            top: 1
          },
          yaxis: {
            show: true,
            showAlways: true,
            tickAmount: 5,
            min: 0,
            max: 1.0,
            forceNiceScale: false,
            floating: false,
          }
        },
      }
    },
    methods: {
      select_city: function (event) {
        console.log(event.target.value);
        console.log(event);
        this.selected_city = event.target.value;
        this.selected_city_column = this.$refs.input_data_ref.table.getDataAtCol(2 + this.input_cities.findIndex(e =>
          e === event
          .target.value));
        this.selected_city_geodata = this.$refs.input_geodata_ref.table.getDataAtCol(this.input_cities.findIndex(e =>
          e === event
          .target.value));
        this.radar_series[0].name = event.target.value
        this.radar_series[0].data = this.$refs.input_graph_ref.table.getDataAtCol(this.input_cities.findIndex(e =>
          e === event
          .target.value));
        this.radar_series[0].data = this.radar_series[0].data.slice(1, 5).map(function (value) {
          return value.toFixed(2);
        });
      },
    },
    computed: {
      first_column_input_data: function () {
        return getCol(this.input_data, 0);
      },
      Max_column: function () {
        return getCol(this.input_data, 24);
      },
      Min_column: function () {
        return getCol(this.input_data, 25);
      },
      GHG: function () {
        return Emission(4.6, 10, 0.5);
      },
      mu: function () {
        return gmean([0.55, 0.69, 1, 0.98, 1]);
      },
      Social_Welfare: function () {
        return Social_Welfare(0.79, 0.80);
      },
    },
    watch: {
      selected_city_column: {
        handler(val, old) {
          //input dataを転置
          this.matrix = transpose(this.input_data);
          //選択した都市のデータを抽出
          this.matrix = this.matrix[(2 + this.input_cities.findIndex(e =>
            e === this.selected_city_column[0]))];
          //更新した値を最大値と比較して、更新
          this.max_column = MaxSort(this.max_column, this.selected_city_column.slice(1, 18));
          this.min_column = MinSort(this.min_column, this.selected_city_column.slice(1, 18));

          //前後の２次元配列を抽出
          this.before_matrix = this.$refs.input_data_ref.table.getData(0, 0, 17, 1 + (this.input_cities.findIndex(e =>
            e === this.selected_city_column[0])))
          this.after_matrix = this.$refs.input_data_ref.table.getData(0, 3 + (this.input_cities.findIndex(e =>
            e === this.selected_city_column[0])), 17, 23);
          //前後のデータ,Max,Minと更新された都市の数値を行列として結合
          this.merged_matrix = pushTwoDimensionalArray(this.before_matrix, transpose([this.selected_city_column]), 1);
          this.merged_matrix = pushTwoDimensionalArray(this.merged_matrix, this.after_matrix, 1);
          //平均値を導出
          var average_array = [];
          for (var i = 1; i < 18; i++) {
            average_array.push(average(this.merged_matrix[i].slice(2, 24)).toFixed(2));
          }
          //テーブルに反映
          this.merged_matrix = pushTwoDimensionalArray(
            pushTwoDimensionalArray(transpose([transpose(this.merged_matrix)[0]]), transpose([
              ["Average 22 cities"].concat(average_array.map(function (element) {
                return String(element);
              }))
            ]), 1),
            this.$refs.input_data_ref.table.getData(0, 2, 17, 23), 1);

          this.$refs.input_data_ref.table.loadData(this.merged_matrix);

          this.merged_matrix = pushTwoDimensionalArray(this.merged_matrix, transpose([
            ["Max"].concat(this.max_column)
          ]), 1);
          this.merged_matrix = pushTwoDimensionalArray(this.merged_matrix, transpose([
            ["Min"].concat(this.min_column)
          ]), 1);
          //console.log(this.merged_matrix);
        },
        deep: true
      }
    }
  };
</script>

